import React, { useState } from "react";
// Material UI
import Tabs from "@material-ui/core/Tabs";
import Tab from "@material-ui/core/Tab";
import Typography from "@material-ui/core/Typography";
import Box from "@material-ui/core/Box";
import Divider from "@material-ui/core/Divider";

// components
import ItemOne from "./tabs/ItemOne";

const Work = () => {
  const [value, setValue] = useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div style={{ flexGrow: 1, display: "flex" }}>
      <Tabs
        orientation="vertical"
        indicatorColor="primary"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Work Vertial Tabs"
      >
        <Tab label="Item 1" />
        <Tab label="Item 2" />
        <Tab label="Item 3" />
        <Tab label="Item 4" />
      </Tabs>
      <Divider orientation="vertical" flexItem />
      {value === 0 && <ItemOne />}
      {value === 1 && (
        <div>
          <Box p={3}>
            <Typography>
              test test test 2<br />
              test test test 2<br />
              test test test 2<br />
              test test test 2<br />
            </Typography>
          </Box>
        </div>
      )}
      {value === 2 && (
        <div>
          <Box p={3}>
            <Typography>
              test test test 3<br />
              test test test 3<br />
              test test test 3<br />
              test test test 3<br />
            </Typography>
          </Box>
        </div>
      )}
    </div>
  );
};

export default Work;
